<template>
    <div>
        <v-header>
            <div slot="right" class="h-right">
                <a @click="help" class="mg-r10"><span class="img help"></span></a></div>
        </v-header>
        <div :class="['service-time',{'b-alone':standalone}]" v-show="!isMine">
            <div class="warp">
                <p class="mg-l10 font-12" style="color:#06af91;">服务时间:&nbsp;{{serviceTime}}</p>
            </div>
        </div>
        <v-content :type="'none'" :style="contentStyl">
            <div v-show="hasService">
                <div class="date-map">
                    <div class="month clear-fix" v-for="item in months">
                        <div class="title">{{item.month}}月&nbsp;&nbsp;{{item.year}}</div>
                        <div class="dis-flex weekday">
                            <p v-for="weekday in weekdays" class="flex-1">
                                {{weekday | week-format}}</p>
                        </div>
                        <div class="days">
                            <ul style="height:{{item.daysLen*0.6}}rem;" :class="'month'+item.month">
                                <li v-for="day in item.days"
                                    :class="['day',day.day == 0 ? 'zero' : 'color-3',
                   $index < 7 ? 'is-top' : '',
                   $index % 7 == 6 ? 'is-right' : '',
                   item.lost!=0 && day.day < item.lost ? 'is-lost' : '',
                   usefulService[day.value+''] && day.day >= item.lost ? 'is-useful':'',
                   day.value == currValue ? 'is-curr':'']"
                                    @click="getDetail(day.value,$event)">
                                    <div v-if="day.day >= item.lost" class="time-split">
                                        <div class="am" v-if="usefulService[day.value+'01']"></div>
                                        <div v-if="usedService[day.value+'01'] &&
                          ((usefulService[day.value+'01'] && usedService[day.value+'01'].total!=0) ||(!usefulService[day.value+'01']))"
                                             :class="['am used',!usefulService[day.value+'01'] ? 'plus':'',
                        usefulService[day.value+'01'] && usedService[day.value+'01'].total <= (usefulService[day.value+'01'].total-1) ? 'half':'',
                        !usefulService[day.value+'01'] && usedService[day.value+'01'].total <= 11 ? 'half':'']">
                                        </div>
                                        <div class="pm" v-if="usefulService[day.value+'02']"></div>
                                        <div v-if="usedService[day.value+'02'] &&
                          ((usefulService[day.value+'02'] && usedService[day.value+'02'].total!=0) ||(!usefulService[day.value+'02']))"
                                             :class="['pm used',!usefulService[day.value+'02'] ? 'plus':'',
                        usefulService[day.value+'02'] && usedService[day.value+'02'].total <= (usefulService[day.value+'02'].total-1) ? 'half':'',
                        !usefulService[day.value+'02'] && usedService[day.value+'02'].total <= 5 ? 'half':'']">
                                        </div>
                                        <div class="em" v-if="usefulService[day.value+'03']"></div>
                                        <div v-if="usedService[day.value+'03'] &&
                          ((usefulService[day.value+'03'] && usedService[day.value+'03'].total!=0) ||(!usefulService[day.value+'03']))"
                                             :class="['em used',!usefulService[day.value+'03'] ? 'plus':'',
                        usefulService[day.value+'03'] && usedService[day.value+'03'].total <= (usefulService[day.value+'03'].total-1) ? 'half':'',
                        !usefulService[day.value+'03'] && usedService[day.value+'03'].total <= 5 ? 'half':'']">
                                        </div>
                                    </div>
                                    <div class="curr" v-if="day.value == currValue"></div>
                                    <p class="val">
                                        {{day.day == 0 ? '' : day.day}}
                                    </p>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
                <div style="height:.1rem;background-color:#fff;"></div>
            </div>
            <div class="none_bg" v-show="!hasService">
                <span class="img bg05"></span>
                <p class="remark">还没有任务进程</p>
            </div>
        </v-content>

        <div :class="['help-box','z-index-max','tran-all-3',showHelp ? 'active':'']">
            <div class="warp">
                <p class="title">日程图标说明</p>
                <span class="img close" @click="hideHelp"></span>
                <div class="help-pic">
                    <!-- <div class="mian-pic">
                      <div class="days">
                        <div class="day is-lost"><p class="val">15</p></div>
                        不可预约
                      </div>
                      <div class="days">
                        <div class="day is-useful">
                          <div class="am time-split" >
                            <div class="used"></div><p class="tip">上午</p>
                          </div>
                          <div class="em time-split" ><p class="tip">下午</p></div>
                          <div class="pm time-split" >
                            <div class="used half"></div><p class="tip">晚上</p>
                          </div>
                          <p class="val">15</p>
                        </div>
                      </div>
                      <div class="days">
                        <div class="day is-none"><p class="val">15</p></div>
                        无安排
                      </div>
                    </div>
                    <div class="detail-pic">
                      <div class="detail">
                        <div class="used"></div>已预约
                      </div>
                      <div class="detail">
                        <div class="useful"></div>可预约
                      </div>
                      <div class="detail">
                        <div class="useful"><div class="half"></div></div>部分可约
                      </div>
                      <div class="detail">
                        <div class="used curr"></div>当前日期选中日期
                      </div>
                    </div> -->
                    <span class="img help-detail"></span>
                    <input type="button" class="know" value="我知道了" @click="hideHelp"/>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
    import VHeader from '../components/Header'
    import VContent from '../components/Content'
    import {DJUtils} from '../assets/js/common'
    import {zeroFill} from '../filter'
    import {toast} from '../assets/js/toast'
    import {url} from '../const'
    import $ from 'jquery'

    export default{
        route: {
            data({from, to}){
                this.showHelp = false;
                this.hasService = false;
                this.isMine = false;
                let _url = url.calendarMine;
                let id = to.params.id;
                if (id) {
                    _url = url.calendarJob;
                    this.contentStyl = {backgroundColor: '#fff', top: '.79rem'};
                } else {
                    id = 0;
                    this.isMine = true;
                    this.contentStyl = {backgroundColor: '#fff'};
                }
                if(!this.isMine && this.standalone){
                    this.contentStyl = {backgroundColor: '#fff', top: '.99rem'};
                }
                return this.$http.get(_url, {jobId: id}).then(
                    ({data: {success, data, resultMsg}}) => {
                        if (success) {
                            this.hasService = true;
                            this.usefulService = data.usefulService;
                            this.usedService = data.usedService;
                            if (!this.isMine) {
                                let useful = this.usefulService;
                                for (let key in useful) {
                                    if (key.length == 8) {
                                        this.serviceTime = useful[key];
                                        break;
                                    }
                                }
                            }
                            let d = new Date();
                            this.currValue = parseInt('' + d.getFullYear() + zeroFill(d.getMonth() + 1) + zeroFill(d.getDate()));
                        } else {
                            toast.alert(resultMsg);
                        }
                    });
            }
        },
        ready(){
            $('.none_bg').css('height', $(window).height() * 0.9 + "px");
            let d = new Date();
            this.currValue = parseInt('' + d.getFullYear() + zeroFill(d.getMonth() + 1) + zeroFill(d.getDate()));
            this.fullDateMap(d.getMonth(), 1);
            this.standalone = DJUtils.isStandalone();
            if(!this.isMine && this.standalone){
                this.contentStyl = {backgroundColor: '#fff', top: '.99rem'};
            }
        },
        data(){
            return {
                // isReady:false,
                // state:false,
                hasService: false,
                showHelp: false,
                isMine: false,
                contentStyl: {backgroundColor: '#fff'},
                months: [],//{month: 1, days: [], lost: 0,year:2016}
                weekdays: ['7', '1', '2', '3', '4', '5', '6'],

                usefulService: {},//可用服务日程,split 1、上午 2、下午 3、晚上
                usedService: {},//已预定服务日程
                currValue: 0,
                serviceTime: '07:00-08:00 14:00-16:00 19:00-21:00',//服务时间
                standalone: false
            }
        },
        methods: {
            help(){
                this.showHelp = true;
                toast.mask(this.hideHelp, true);
            },
            hideHelp(){
                this.showHelp = false;
                toast.hide_mask();
            },
            fullDateMap(m, n){//当月 填充的月份
                let date, lost = 0, days = [], month = 0;
                for (let i = m; i <= (m + n); i++) {
                    date = new Date();
                    if (parseInt(i) == date.getMonth()) {//计算当月已过去的时间
                        lost = date.getDate();
                    } else if (parseInt(i) < date.getMonth()) {
                        lost = new Date(date.getFullYear(), parseInt(i) + 1, 0).getDate() + 1;
                    } else {
                        lost = 0;
                    }
                    if (parseInt(i) == 12) {//下个月是1月
                        date.setFullYear(date.getFullYear() + 1);
                        date.setMonth(0);
                    } else if (parseInt(i) == -1) {//改月份为12月
                        date.setFullYear(date.getFullYear() - 1);
                        date.setMonth(11);
                    } else {
                        date.setMonth(i, 1);
                    }
                    //格式后的date
                    days = this.dayNumOfMon(date);
                    month = date.getMonth() + 1;
                    this.months.push({
                        month: month,
                        days: days,
                        year: date.getFullYear(),
                        lost: lost,
                        daysLen: Math.ceil(days.length / 7)
                    });
                }
                console.log(JSON.stringify(this.months));
            },
            dayNumOfMon(d){
                let days = [];
                let max = new Date(d.getFullYear(), (d.getMonth() + 1), 0).getDate();
                let noWeek = new Date(d.getFullYear(), d.getMonth(), 1).getDay();//获取该天是周几
                for (let i = 0; i < noWeek; i++) {//填充没有的时间
                    days.push({day: 0, value: 0});
                }
                for (let i = 1; i <= max; i++) {
                    days.push({
                        day: i,
                        value: parseInt('' + d.getFullYear() + zeroFill(d.getMonth() + 1) + zeroFill(i))
                    });
                }
                return days;
            },
            getDetail(value, event){
                let d = new Date();
                let val = parseInt('' + d.getFullYear() + zeroFill(d.getMonth() + 1) + zeroFill(d.getDate()));
                if (value < val) {
                    return;
                }
                let day = $(event.currentTarget);
                if ((day.is('.is-useful'))) {
                    this.currValue = value;
                }
                let usedService = this.usedService;
                let time = '';
                for (let i in usedService) {
                    if (usedService[i].value == value) {
                        this.currValue = value;
                        time += usedService[i].timeScaleStr;
                    }
                }
                if (time) {
                    toast.alert('<p class="text-left">已预约：</p>  ' + time, 3000);
                } else if (day.is('.is-useful')) {
                    toast.alert('无预约', 3000);
                }
                // let usefulTime = this.usefulTime;
                // for(let i in usefulTime){
                //   if(usefulTime[i].value == value){
                //     this.serviceTime = usefulTime[i].serviceTime;
                //   }
                // }
            }
        },
        components: {VHeader, VContent}
    }
</script>
<style scoped>
    .service-time {
        height: .35rem;
        line-height: .35rem;
        position: fixed;
        top: .44rem;
        left: 0;
        right: 0;
    }

    .service-time.b-alone {
        top: .64rem;
    }

    .service-time .warp {
        background-color: #e6fff6;
        height: 100%;
    }

    .date-map {
        background-color: #fff;
    }

    .date-map .title {
        line-height: .45rem;
        height: .45rem;
        font-size: .16rem;
        text-align: center;
        color: #333;
    }

    .date-map .weekday {
        background-color: #e0e0e0;
        font-size: .12rem;
        text-align: center;
        height: .25rem;
        line-height: .25rem;
    }

    .days .day {
        width: 14.28%;
        height: .6rem;
        background-color: #f9f9f9;
        font-size: .16rem;
        text-align: center;
        float: left;
        line-height: .6rem;
        border-bottom: .01rem solid #fff;
        border-right: .01rem solid #fff;
        position: relative;
    }

    .day.is-top {
        border-top: none;
    }

    .day.is-right {
        border-right: none;
    }

    .day.is-lost {
        background-color: #ebebeb;
        color: #bbb;
    }

    .day.zero {
        background-color: #fff;
    }

    .day .val {
        position: absolute;
        width: 100%;
    }

    /*.is-useful .val {*/
    /*color: #099e75;*/
    /*}*/

    .time-split .am, .time-split .pm, .time-split .em {
        position: absolute;
        width: 100%;
        height: 33.3%;
        background-color: #d0f5e8;
    }

    .time-split .am {
        top: 0;
    }

    .time-split .pm {
        top: 33.3%;
    }

    .time-split .em {
        top: 66.6%;
        border-bottom: none;
    }

    .time-split .used {
        background-color: #58cdbb;
    }

    .time-split .plus {
        background-color: #dfc66c;
    }

    .used.half {
        width: 50%;
    }

    .day .curr {
        position: absolute;
        right: 0;
        top: 0;
        border-top: .1rem solid #ff7930;
        border-left: .1rem solid transparent;
    }

    .help-box {
        position: fixed;
        top: 100%;
        left: 50%;
        margin-left: -1.5rem;
        width: 3rem;
        height: 0;
    }

    .help-box.active {
        top: 15%;
    }

    .help-box .warp {
        padding-top: 130%;
        background-color: #dce6e6;
        height: 100%;
        position: relative;
        border-radius: .1rem;
    }

    .help-box .title {
        font-size: .16rem;
        color: #333;
        text-align: center;
        position: absolute;
        width: 100%;
        top: 0;
        height: .5rem;
        line-height: .5rem;
        background-color: #0ac8a5;
        border-top-right-radius: .1rem;
        border-top-left-radius: .1rem;
    }

    .help-box .help-pic {
        width: 100%;
        height: 100%;
        padding: .2rem;
    }

    .img.help-detail {
        width: 90%;
        left: 5%;
        position: absolute;
        top: .65rem;
        height: 65%;
        background-size: 100% 100%;
        background-image: url(../assets/img/icon/calendar-help.png);
    }

    .help-pic .know {
        position: absolute;
        width: 40%;
        bottom: .15rem;
        left: 30%;
        height: .3rem;
        border-radius: .3rem;
        border: .01rem solid #d1d7d7;
        color: #888;
        background-color: #dce6e6;
        text-align: center;
    }

    .img.close {
        width: .15rem;
        height: .15rem;
        background-size: .15rem;
        position: absolute;
        right: .15rem;
        top: .15rem;
        background-image: url(../assets/img/icon/close04.png);
    }

    .img.bg05 {
        background-image: url(../assets/img/bg/null_icon05.png);
    }
</style>



